@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.add-newWorking {
  position: absolute;
  width: 100%;
}

:root {
  font-size: 16px;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans TC", sans-serif;
}

div::-webkit-scrollbar {
  display: none;
}

.appContainer {
  width: 100%;
  height: 100vh;
  background-color: #fbfbfb;
  margin: 0 auto;
}

.container {
  padding: 0 17px;
}

a {
  text-decoration: none;
}

/* Chrome, Safari, Edge, Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

input:focus {
  outline: 0;
}

input,
select {
  -webkit-appearance: none;
  border-radius: 0;
}

a,
button,
input,
textarea,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.navBar {
  width: 100%;
  height: 60px;
  /* background-color: #fff; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
}

.navBar h1 {
  font-weight: 400;
}

.navBar .none {
  display: none;
}

.tabBar {
  width: 100%;
  max-height: 62px;
  background-color: #fafafa;
  /* position: absolute;
    bottom: 0; */
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  padding-bottom: 20px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.workTab,
.lightingTab,
.settingTab {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.normalState {
  color: transparent;
}

.workTab {
  margin-left: 36px;
}

.settingTab {
  margin-right: 36px;
}

.workTabName p {
  font-size: 13px;
  color: #286bb3;
  font-weight: 500;
}
.lightingTabName p {
  font-size: 13px;
  color: #286bb3;
  font-weight: 500;
}
.settingTabName p {
  font-size: 13px;
  color: #286bb3;
  font-weight: 500;
}

.statusContainer {
  width: 800px;
  height: 76px;
  background-color: #fff;
  box-shadow: #eee;
  box-shadow: 0 0 10px 0 #e5e7e8;
  border-radius: 15px;
  display: flex;
  align-items: center;
}

.statusImg {
  padding-left: 21px;
  align-items: center;
}

.statusTime {
  padding-left: 16px;
}

.statusTime p:nth-child(1) {
  font-size: 16px;
}

.statusTime p:nth-child(2) {
  color: #286bb3;
  font-weight: 300;
  font-size: 13px;
}

.workContainer {
  width: 100%;
  height: 68px;
  border-radius: 18px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

#workID {
  background-color: #ffbd00;
}

#yellow {
  background-color: #ffbd00;
}
#blue {
  background-color: #00c8d3;
}
#green {
  /* background-color: #cfdc38; */
  background-color: #43d7b1;
}
#red {
  background-color: #ff8449;
}
#purple {
  background-color: #7c93ff;
}

.workImg {
  margin-left: 24px;
  display: flex;
  align-items: center;
}

.workContent {
  margin-left: 9px;
}

.workContent p,
.rightArea .text p {
  font-size: 12pt;
  color: #fff;
  font-weight: 400;
}

.workContent p {
  width: 110px;
}

.workArea {
  display: flex;
  align-items: center;
}

.rightArea {
  display: flex;
  align-items: center;
}

.rightArea .text {
  display: flex;
  margin-right: 42px;
}

.deleteArea {
  background-color: rgba(255, 255, 255, 0.502);
  width: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: 900;
}

.calendar {
  box-sizing: border-box;
  font-size: 1rem;
}
.weekCalendar {
  margin-bottom: 115px;
}

.dayWork {
  width: 100%;
  padding: 15px 0;
  background-color: #fff;
  box-shadow: 0px -15px 30px -15px #e5e8ea;
  height: 270px;
}

.calendar .header {
  text-align: center;
  min-height: 2rem;
  line-height: 2rem;
  color: var(--black);
  font-family: var(--font-book);
  font-weight: 300;
  display: flex;
  margin-bottom: 10px;
}

.calendar .day-names {
  display: flex;
  margin: 0 auto;
  justify-content: space-around;
}

.day-workHour,
.hour {
  display: flex;
  margin: 0 auto;
  justify-content: space-around;
}

.calendar .header .previous {
  flex: 1 1;
  text-align: left;
  margin-left: 1.2rem;
}

.calendar .header .next {
  flex: 1 1;
  text-align: right;
  margin-right: 1.2rem;
}

.calendar .week {
  height: 44px;
  line-height: 44px;
  width: 20px;
  display: flex;
  justify-content: space-around;
}

.calendar .week div {
  width: 100%;
}

.dayContainer {
  display: flex;
  justify-content: space-around;
}

.calendar .day {
  width: 30px;
  height: 30px;
  align-items: center;
  text-align: center;
}

.calendar .day-workHour {
  color: #aaaaaa;
  font-size: 13pt;
  font-weight: 300;
}

.calendar .hour {
  color: #aaa;
  font-size: 8.5pt;
  font-weight: 200;
  padding-bottom: 12px;
  margin-top: 2px;
}

.calendar .day {
  /* color: #286BB3; */
  color: #a9bbc9;
  font-size: 14pt;
  font-weight: 400;
}

.calendar .day-names {
  /* color: #A9BBC9; */
  color: #e4eaee;
  font-size: 12pt;
}

.calendar .day div.before {
  color: var(--grey);
}

.calendar .day div.selected {
  background-color: #fff;
  color: #286bb3;
  font-size: 14pt;
  font-weight: 400;
  box-shadow: 10px -5px 10px -7px #e5e8ea, -10px -5px 10px -7px #e5e8ea,
    0 -6px 10px -5px #e5e8ea;
  border-radius: 25px 25px 0 0;
}

.calendar .day div.selectedNull {
  background-color: #fff;
  color: #286bb3;
  font-size: 14pt;
  font-weight: 400;
  box-shadow: 10px -5px 10px -7px #e5e8ea, -10px -5px 10px -7px #e5e8ea,
    0 -6px 10px -5px #e5e8ea;
  border-radius: 25px 25px 0 0;
  padding-bottom: 45px;
}

div.selected .day-names {
  color: #a9bbc9;
}

div.selectedNull .day-names {
  color: #a9bbc9;
}

.calendar .day div.today {
  color: #286bb3;
}

.Every-dot {
  display: flex;
  justify-content: center;
}

.dot {
  width: 4px;
  height: 4px;
  border-radius: 10px;
  /* background-color: #FFC926; */
  background-color: #3d3834;
  margin: 8px 0;
}

div.selected .dot,
div.selectedNull .dot {
  background-color: #ffc926;
}

div.selected .day-workHour,
div.selected .hour {
  color: #000;
}

.hourArea {
  display: flex;
  justify-content: space-around;
}

.workingSchedule {
  overflow: auto;
  height: 100%;
}

.marginSpace {
  margin: 15px 0;
}

.stateArea {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}

.calenderArea {
  margin-top: 30px;
}

.calenderArea h2 {
  font-weight: 400;
  font-size: 16pt;
}

.spacingBlock {
  width: 50px;
}

.itemContainer {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  position: relative;
}

.itemContainer p:nth-child(2),
.normal {
  color: #00c8d3;
  padding-right: 22px;
}

.itemContainer .selector {
  position: absolute;
  right: 22px;
}

.selector select {
  border: none;
  font-size: 16px;
  color: #00c8d3;
  background-color: #fbfbfb;
}

.itemContainer .normalSelect {
  color: transparent !important;
}

.lightingContainer {
  margin-top: 30px;
}

.connectState {
  font-size: 14px;
  padding-bottom: 12px;
  margin-left: 17px;
  padding-top: 5px;
  color: #00c8d3;
  font-weight: 500;
}

.unConnectState {
  font-size: 14px;
  padding-bottom: 12px;
  margin-left: 17px;
  padding-top: 5px;
  color: #fff;
  font-weight: 500;
}

.dateSetting {
  background-color: #fff;
  height: 100vh;
}

.noDateSetting {
  display: none;
}

.dateSettingBar {
  padding-top: 25px;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-between;
}

.dateSettingBar div {
  color: #4a90e2;
  font-weight: 500;
  border: none;
  background-color: transparent;
  font-size: 16px;
}

.nameInput {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5px;
}

.nameInput input {
  border: none;
  border-radius: none;
  border-bottom: 1px solid #979797;
  font-size: 22px;
  padding-right: 40px;
  padding-bottom: 5px;
  letter-spacing: 1px;
  color: #000;
  outline: none;
  width: 250px;
}
.nameInput input::-webkit-input-placeholder {
  color: #a9bbc9;
}
.nameInput input:-ms-input-placeholder {
  color: #a9bbc9;
}
.nameInput input::placeholder {
  color: #a9bbc9;
}

.colorSelector {
  position: relative;
  display: flex;
}

.colorOption {
  position: absolute;
  background-color: #f5f5f5;
  width: 24px;
  border-radius: 0 0 3px 3px;
  top: 20px;
}

.noneOption {
  display: none;
}

.colorSelector .colorChoose {
  background-color: #f5f5f5;
  width: 24px;
  border-radius: 3px;
}

.colorOption .colorItem,
.colorChoose .colorDefaultItem {
  border-radius: 3px;
  margin: 4px 3.5px;
  width: 16px;
  height: 16px;
}

.eventForm p:nth-child(2),
.eventForm p:nth-child(4),
.eventForm p:nth-child(6) {
  color: #286bb3;
  font-size: 17px;
  margin-top: 14px;
  font-weight: 400;
}

.deadlinePicker,
.hourPicker,
.datePicker {
  margin-top: 10px;
}

.hourPicker {
  display: flex;
}

.alertText {
  font-size: 12px;
  color: #e15151;
  letter-spacing: 0.2px;
  margin-top: 5px;
}

.nameAlert {
  color: #e15151;
}

.prompt {
  color: #727a83;
  font-weight: 400;
  font-size: 13px;
  margin-top: 5px;
  letter-spacing: 0.2px;
}

.monthCalendar {
  box-sizing: border-box;
  font-size: 1rem;
  max-width: 300px;
}

.monthCalendar .dayName {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  align-items: center;
}

.monthCalendar .dayName .week {
  height: 36px;
  line-height: 36px;
  width: calc(100% / 7);
  display: flex;
  justify-content: space-around;
  color: #b7bec5;
  font-weight: 600;
}

.monthCalendar .header .previous {
  flex: 1 1;
  text-align: left;
  margin-left: 1rem;
}

.monthCalendar .header .next {
  flex: 1 1;
  text-align: right;
  margin-right: 1rem;
}

.monthCalendar .day {
  font-size: 14px;

  position: relative;
  width: calc(100% / 7);
  display: inline-block;
  background-color: white;
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
  z-index: 1;
  text-align: center;
}

.monthCalendar .day div {
  display: inline-block;
  width: 24px;
  position: relative;
  z-index: 100;
  margin-bottom: 5px;
}

.monthCalendar .day .date {
  color: #7c86a2;
  font-weight: 500;
  margin-bottom: 5px;
}

.monthCalendar .day div.before,
.monthCalendar .day div.after {
  color: #d7dae2;
  font-weight: 500;
}

.monthCalendar .day div.selected {
  color: #d3ac4c;
  font-weight: bold;
}

.monthCalendar .day div.today {
  color: #526ca1;
  font-weight: 500;
}

.monthCalendar .day .oldHours {
  font-size: 10px;
  line-height: 16px;
  width: 23px;
  height: 16px;
  text-align: center;
  vertical-align: top;
  border-radius: 20px;
  background-color: #f7f8fc;
  color: #8c96ab;
}

.monthCalendar .day .oldBefore {
  font-size: 10px;
  line-height: 16px;
  width: 23px;
  height: 16px;
  text-align: center;
  vertical-align: top;
  border-radius: 20px;
  background-color: #fefeff;
  color: #dde0e6;
}

.monthCalendar .day .oldHoursNull {
  line-height: 16px;
  width: 20px;
  height: 16px;
  vertical-align: top;
}

.monthCalendar .day .newHours {
  width: 23px;
  height: 16px;
  vertical-align: top;
  line-height: 16px;
  background-color: #a2b3c5;
  border-radius: 20px;
  color: #fff;
  font-size: 10px;
}

.monthCalendar .day .newHoursNull {
  width: 23px;
  height: 16px;
  vertical-align: top;
  color: transparent;
  border-radius: 20px;
  margin-top: 2px;
}

.monthCalendar .hourInput input:nth-child(1) {
  border: none;
  background-color: #a9bbc9;
  width: 300px;
  justify-content: center;
  color: #fff;
  padding-left: 20px;
  margin-top: 5px;
}

.monthCalendar .hourInput input:nth-child(1)::-webkit-input-placeholder {
  color: #fff;
  letter-spacing: 0.5px;
}

.monthCalendar .hourInput input:nth-child(1):-ms-input-placeholder {
  color: #fff;
  letter-spacing: 0.5px;
}

.monthCalendar .hourInput input:nth-child(1)::placeholder {
  color: #fff;
  letter-spacing: 0.5px;
}

.monthCalendar .hourInput input:nth-child(2) {
  border: none;
  color: #fff;
  background-color: transparent;
  position: absolute;
  right: 12px;
  height: 45px;
  font-weight: 500;
}

.monthCalendar .hourInput {
  border-radius: none;
  position: relative;
  display: flex;
  height: 40px;
}

.monthCalendar .noneInput {
  display: none;
}

.monthCalendar .triangle {
  position: absolute;
  top: 70px;
  right: 15px;
  width: 15px;
}

.monthCalendar .triangleNone {
  display: none;
}

.monthHeader {
  width: 280px;
  display: flex;
  margin: 10px 0;
  margin-left: 12px;
  justify-content: space-between;
}

.monthHeader .changeButton {
  display: flex;
}

.changeButton .previous,
.changeButton .next {
  color: #8c96ab;
  background-color: #f7f8fc;
  width: 26px;
  border-radius: 40%;
  display: flex;
  justify-content: center;
  height: 22px;
  line-height: 18px;
  align-self: center;
  margin-left: 5px;
}

.monthHeader .current {
  color: #7c86a2;
  font-size: 15px;
  letter-spacing: 1px;
  font-weight: 600;
}

